import React, { useEffect, useState } from "react";
import styles from "../../index.module.css";
import cx from "classnames";

// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation } from "swiper";

// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation";
import axiosBaseURl from "../axios";

const HomeWidget = () => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false); //state for loading
  const [errorMsg, setErrorMsg] = useState("");

  //Fetching data from api by using id attributes passed in the render div element
  const merchantId = document.getElementById("khooobsooorat-widget").dataset
    .merchantId;

  //Fetch video data
  useEffect(() => {
    const fetchVideoReviews = async () => {
      setLoading(true);
      try {
        const response = await axiosBaseURl.get(
          `/home-widget/getVideos?merchantId=${merchantId}`
        );
        setData(response.data);
        setLoading(false);
      } catch (err) {
        setData(null);
        setLoading(false);
        console.log(err);
        setErrorMsg(
          err.response.data.message +
            ". Add videos from your dashboard and try again!"
        );
      }
    };

    //Running api functions
    fetchVideoReviews();
  }, [merchantId]);

  const { merchant, videos } = data;

  console.log(data);
  return (
    <div id='home-widget' className={styles.homeVideoWrapper}>
      <div
        className={cx(
          styles.reviews__gallery__header,
          "d-flex justify-content-between"
        )}
      >
        <h4 className={cx(styles.header, "mb-0")}>
          Unbiased Reviews {merchant && "on " + merchant.name} by{" "}
          <a
            target='_blank'
            href='https://www.khooobsooorat.com'
            rel='noreferrer'
          >
            Khooobsooorat
          </a>
        </h4>
      </div>
      <hr className='mb-0' />
      {loading ? (
        <div className='mt-3 text-center'>
          <span
            className='spinner-border text-danger text-center'
            role='status'
            aria-hidden='true'
          />
        </div>
      ) : (
        <Swiper
          spaceBetween={20}
          slidesPerView={"auto"}
          navigation
          modules={[Navigation]}
          className='mt-3'
        >
          {videos &&
            videos.map((item, idx) => {
              return (
                <SwiperSlide key={idx}>
                  <div className={styles.video__frame}>
                    <iframe
                      width='222'
                      height='385'
                      src={`https://www.youtube.com/embed/${item.ks_reviews.video_name}`}
                      title='Youtube'
                      allow='accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture'
                    ></iframe>
                  </div>
                </SwiperSlide>
              );
            })}
        </Swiper>
      )}
    </div>
  );
};

export default HomeWidget;
