import React from "react";
import cx from "classnames";
import styles from "../../../index.module.css";
import { Cross } from "../../svg";

const FilterComponent = (props) => {
  const { filterTagsClone, activeTag, setActiveTag } = props;

  const handleClick = (item) => {
    if (activeTag.includes(item)) {
      setActiveTag([
        ...activeTag.filter((existingItem) => existingItem !== item),
      ]);
    } else {
      setActiveTag((existingItems) => {
        return [...existingItems, item];
      });
    }
  };
  // console.log("activeTag", activeTag);

  return (
    <div className={cx(styles.reviews__filter, "row mt-2")}>
      <div className={cx(styles.filter, "col ")}>
        {activeTag.map((item, idx) => {
          return (
            <button
              key={idx}
              onClick={() => handleClick(item)}
              className={cx(styles.tag, styles.active)}
            >
              {item}
              <Cross />
            </button>
          );
        })}
        {filterTagsClone &&
          filterTagsClone
            .filter((item) => item !== "0" && item !== null)
            .map((item, idx) => {
              return (
                <button
                  key={idx}
                  onClick={() => handleClick(item)}
                  className={cx(
                    styles.tag
                    /*  activeTag.includes(idx) ? styles.active : "" */
                  )}
                >
                  {item}
                  {/* {activeTag.includes(idx) ? <Cross /> : null} */}
                </button>
              );
            })}
      </div>
    </div>
  );
};

export default FilterComponent;
