import React, { useState, useEffect, useCallback } from "react";
import styles from "../../index.module.css";
import cx from "classnames";
import ReviewComponent from "../subComponents/ReviewComponent";
import VideoSliderComponent from "../subComponents/VideoSliderComponent";
import SortComponent from "../subComponents/SortComponent";
import ScoreComponent from "../subComponents/ScoreComponent";
import { ArrowDown } from "../svg";
import axiosBaseURl from "../axios";

const Home = () => {
  const [textdata, settextdata] = useState([]);
  const [videodata, setvideodata] = useState([]);
  const [sort, setSort] = useState("date");
  const [loading, setLoading] = useState(false);
  const [loginLoading, setloginLoading] = useState(false);
  const [visible, setVisible] = useState(6);
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [activeTag, setActiveTag] = useState([]);
  const showMoreWorks = () => {
    setVisible((prevValue) => prevValue + 6);
  };

  //Fetching data from api by using id attributes passed in the render div element
  const merchantId = document.getElementById("khooobsooorat-widget").dataset
    .merchantId;
  const merchantProductId = document.getElementById("khooobsooorat-widget")
    .dataset.merchantProductid;

  //Fetch text reviews
  const fetchTextReviews = useCallback(async () => {
    setLoading(true);
    try {
      const response = await axiosBaseURl.get(
        `/review/getTextReviewsOfProduct/${merchantId}/${merchantProductId}?page=1&size=${visible}&filterBy=${sort}${activeTag
          .map((item) => {
            return `&filterTag=${item}`;
          })
          .join("")}`
      );
      settextdata(response.data);
      setLoading(false);
    } catch (err) {
      settextdata(null);
      console.log(err);
    }
  }, [activeTag, merchantId, merchantProductId, sort, visible]);

  useEffect(() => {
    localStorage.getItem("ks_user_token")
      ? setIsLoggedIn(true)
      : setIsLoggedIn(false);

    //Fetch video reviews
    const fetchVideoReviews = async () => {
      try {
        const response = await axiosBaseURl.get(
          `/review/getVideoReviewsOfProduct/${merchantId}/${merchantProductId}?page=1&size=${visible}&filterBy=${sort}${activeTag
            .map((item) => {
              return `&filterTag=${item}`;
            })
            .join("")}`
        );
        setvideodata(response.data);
      } catch (err) {
        setvideodata(null);
        console.log(err);
      }
    };

    //Running both the api functions
    fetchVideoReviews();
    fetchTextReviews();
  }, [
    activeTag,
    fetchTextReviews,
    merchantId,
    merchantProductId,
    sort,
    visible,
  ]);

  //Destructuring text and video reviews data to pass in components
  const {
    productId,
    averageRating,
    highlightedRatings,
    textReviews,
    totalTextReviews,
    filterTags,
  } = textdata;
  const { totalVideoReviews, brandName, videoReviews } = videodata;

  //Delete activetags from filterTags array by using a clone
  const tagsToDelete = new Set(activeTag);
  const filterTagsClone =
    filterTags &&
    filterTags.filter((item) => {
      return !tagsToDelete.has(item);
    });

  //Calculating total review data for score component
  const totalReviews = totalTextReviews + totalVideoReviews;

  //Logout Function
  const handleLogout = () => {
    setloginLoading(true);
    setTimeout(() => {
      localStorage.removeItem("ks_user_token");
      localStorage.removeItem("hash");
      setIsLoggedIn(false);
      setloginLoading(false);
    }, 1000);
  };

  // console.log("mydata", totalTextReviews);
  // console.log("mydata", visible);
  // console.log("haha", averageRating);

  return (
    <div id='review-widget'>
      <div className={styles.reviews}>
        <div className={styles.reviews__header}>
          <div className={styles.reviews__gallery}>
            <div
              className={cx(
                styles.reviews__gallery__header,
                "d-flex justify-content-between"
              )}
            >
              <h4 className={styles.header}>
                Unbiased Reviews by{" "}
                <a
                  target='_blank'
                  href='https://www.khooobsooorat.com'
                  rel='noreferrer'
                >
                  Khooobsooorat
                </a>
                {/* <span className={styles.ksname}>Khooobsooorat</span> */}
              </h4>
              {isLoggedIn ? (
                <button className='btn p-0 px-2' onClick={handleLogout}>
                  {loginLoading ? (
                    <span
                      className='spinner-border spinner-border-sm'
                      role='status'
                      aria-hidden='true'
                    />
                  ) : (
                    "Logout"
                  )}
                </button>
              ) : (
                <button
                  className='btn p-0 px-2'
                  data-bs-toggle='modal'
                  data-bs-target='#loginModal'
                >
                  Login
                </button>
              )}
            </div>
            <hr className='mb-0' />
            <ScoreComponent
              totalReviews={totalReviews}
              averageRating={averageRating}
              featureData={highlightedRatings}
              brandName={brandName}
              setIsLoggedIn={setIsLoggedIn}
              productId={productId}
              fetchTextReviews={fetchTextReviews}
              filterTagsClone={filterTagsClone}
              activeTag={activeTag}
              setActiveTag={setActiveTag}
            />
            {videoReviews && videoReviews.length !== 0 && (
              <hr className='mb-4' />
            )}
            <VideoSliderComponent
              brandName={brandName}
              videoReviews={videoReviews}
            />
          </div>
        </div>
        <hr className='mt-0' />
        <div className={styles.reviews__body}>
          {/* <SortComponent setSort={setSort} /> */}
          <ReviewComponent
            fetchTextReviews={fetchTextReviews}
            reviewData={textReviews}
          />
        </div>
        <div className={styles.reviews__footer}>
          {loading ? (
            <div className='text-center'>
              <span
                className='spinner-border text-danger text-center'
                role='status'
                aria-hidden='true'
              />
            </div>
          ) : (
            <div className={styles.read__more}>
              {totalTextReviews <= visible ? (
                <span className={cx(styles.text, "me-2")}>No more reviews</span>
              ) : (
                <span
                  className={cx(styles.text, "me-2")}
                  onClick={showMoreWorks}
                >
                  Read more reviews
                  <ArrowDown />
                </span>
              )}
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default Home;
