import React, { useState } from "react";
import Rating from "../Rating";
import styles from "../../../index.module.css";
import cx from "classnames";
import FilterComponent from "../FIlterComponent";
import { Caret, Heart, Pencil, StarFullWhite } from "../../svg";
import LoginForm from "../Forms/LoginForm";
import "animate.css";

const ScoreComponent = (props) => {
  const {
    productId,
    brandName,
    totalReviews,
    averageRating,
    featureData,
    setIsLoggedIn,
    fetchTextReviews,
    filterTagsClone,
    activeTag,
    setActiveTag,
  } = props;

  //Expand functionality
  const [expand, setExpand] = useState(false);
  const handleClick = () => {
    setExpand(!expand);
  };

  // console.log("haha", totalReviews, averageRating);

  return (
    <div className={styles.score__wrapper}>
      <h4
        onClick={handleClick}
        className={cx(
          styles.score__header,
          "d-flex align-items-center m-0 py-4"
        )}
      >
        Khooobsooorat Score{" "}
        <div className={cx("caret ms-3", expand ? "up" : null)}>
          <Caret />
        </div>
      </h4>
      {/* <hr className='my-0' /> */}
      <div className={cx(styles.score__stats, " row pb-3")}>
        <div
          className={cx(styles.stars, "col-5 d-flex gap-4 align-items-center")}
        >
          <Rating rating={averageRating} />
          <h5 className={styles.date}>
            {!Number.isNaN(totalReviews) && totalReviews} Reviews
          </h5>
        </div>
        <div
          className={cx(styles.likes, "col-3 d-flex gap-2 align-items-center")}
        >
          <Heart />
          <h5 className={styles.date}>
            {averageRating && (averageRating * 20).toFixed(0)}%
          </h5>
        </div>
        <div
          className={cx(
            styles.overall__rating,
            "col d-flex gap-2 align-items-center"
          )}
        >
          <div
            className={cx(
              styles.rate__point,
              " d-flex gap-1 align-items-center"
            )}
          >
            <span>{averageRating && averageRating.toFixed(1)}</span>
            <StarFullWhite />
          </div>
          <h5 className={styles.date}>Reviews</h5>
        </div>
      </div>
      <div
        hidden={!expand}
        className={cx(
          styles.overview,
          "my-4 mt-sm-3 mb-sm-5 animate__animated animate__fadeIn"
        )}
      >
        {featureData &&
          featureData.map((item, idx) => {
            return (
              <div key={idx} className={styles.grid__item}>
                <progress
                  id='file'
                  className='me-4'
                  value={item.averageRating * 20}
                  max='100'
                />
                <label htmlFor='file'>{item.name}</label>
              </div>
            );
          })}
      </div>
      <FilterComponent
        filterTagsClone={filterTagsClone}
        activeTag={activeTag}
        setActiveTag={setActiveTag}
      />
      <div className={cx(styles.review__product, " d-flex gap-3")}>
        <button
          className={cx(styles.review__btn, "btn d-flex align-items-center")}
          data-bs-toggle='modal'
          data-bs-target='#loginModal'
        >
          <Pencil /> Review this product
        </button>
        <LoginForm
          featureData={featureData}
          brandName={brandName}
          setIsLoggedIn={setIsLoggedIn}
          productId={productId}
          fetchTextReviews={fetchTextReviews}
        />
        <p>
          Share your thoughts with other customers by sharing an{" "}
          <a href='#review'>unbiased review</a>
        </p>
      </div>
    </div>
  );
};

export default ScoreComponent;
