import React, { useEffect, useState } from "react";
import { Cross } from "../../svg";
import RatingStars from "../Rating";
import styles from "../../../index.module.css";
import Rating from "react-rating";
import jwtDecode from "jwt-decode";
import { StarFull, StarFullEmpty } from "../../svg";
import axiosBaseURl from "../../axios";

const LoginForm = (props) => {
  const { productId, featureData, brandName, setIsLoggedIn, fetchTextReviews } =
    props;

  const [loading, setLoading] = useState(false); //state for loading
  const [verifyLoading, setverifyLoading] = useState(false); //state for loading
  const [loginScreen, setloginScreen] = useState("sendotp");
  const [phoneNo, setPhoneNo] = useState("");
  const [isUser, setIsUser] = useState(false);
  const [userInfo, setUserInfo] = useState({ firstName: "", lastName: "" });
  const [otp, setOtp] = useState("");
  const [loginErrorMsg, setloginErrorMsg] = useState("");
  const [errorMsg, setErrorMsg] = useState("");
  const [canResend, setCanResend] = useState(true);
  const [otpcountdown, setotpcountdown] = useState(60);
  const [reviewTitle, setReviewTitle] = useState("");
  const [reviewText, setReviewText] = useState("");
  const [featureValue, setFeatureValue] = useState({});
  const [file, setFile] = useState([]);
  const [imagePreview, setImagePreview] = useState([]);

  //Calculate average feature rating by taking values from featureValue object
  const avgRating =
    Object.keys(featureValue).length !== 0
      ? Object.values(featureValue).reduce((a, b) => a + b, 0) /
        Object.values(featureValue).length
      : 0;

  //Countdown
  useEffect(() => {
    var otpcountdown = 60;
    const timer = setInterval(() => {
      if (otpcountdown >= 0 && loginScreen === "verifyotp") {
        setotpcountdown(otpcountdown--);
      }
    }, 1000);
    return () => clearTimeout(timer);
  }, [loginScreen]);

  //Function for sending otp from number input taken
  const sendOtp = async (fromResend) => {
    setLoading(true);

    //setting body object to pass in post api
    const body = {
      phone: parseInt(phoneNo),
    };

    //api call
    try {
      const response = await axiosBaseURl.post("/reviewerAuth/sendOtp", body);
      setIsUser(response.data.isUser);
      setLoading(false);
      setErrorMsg("");
      setloginErrorMsg("");
      setReviewTitle("");
      setReviewText("");
      setFeatureValue({});
      setFile([]);
      setImagePreview([]);
      console.log(response);
      localStorage.setItem("hash", response.data.hash);
      if (response.data.messageStatus === "success") {
        setloginScreen("verifyotp");
      }
      if (fromResend === "true") {
        setCanResend(false);
      }
    } catch (err) {
      setLoading(false);
      setloginErrorMsg(err.response.data.message + ". Try Again");
      console.log(err);
    }
  };

  //Function for verifying otp with number, hash and otp received
  const verifyOtp = async () => {
    setverifyLoading(true);

    //setting body object to pass in post api
    const body = {
      phone: parseInt(phoneNo),
      hash: localStorage.getItem("hash"),
      otp: parseInt(otp),
      firstName: userInfo.firstName,
      lastName: userInfo.lastName,
    };

    //api call
    try {
      const response = await axiosBaseURl.post("/reviewerAuth/verifyOtp", body);
      setverifyLoading(false);
      console.log(response);
      localStorage.setItem("ks_user_token", response.data.token);
      setloginScreen("addreview");
      setIsLoggedIn(true);
      setPhoneNo("");
      setOtp("");
      setloginErrorMsg("");
    } catch (err) {
      setverifyLoading(false);
      setloginErrorMsg(err.response.data.message + ". Try Again");
      console.log(err);
    }
  };

  //Function for adding review from form data
  const addReview = async () => {
    setLoading(true);

    //decode jwt token
    try {
      // Check if no token
      if (!localStorage.getItem("ks_user_token")) {
        console.log("No token available");
      } else {
        var token = localStorage.getItem("ks_user_token");
      }
      var decoded = jwtDecode(token);
    } catch (error) {
      console.log(error);
    }

    //setting body object to pass in post api
    const formData = new FormData();
    formData.append("reviewText", reviewText);
    formData.append("reviewTitle", reviewTitle);
    formData.append("rating", avgRating.toString());
    formData.append("highlightedReviews", JSON.stringify(featureValue));
    formData.append("productId", productId);
    formData.append("customerId", decoded.sub);
    file.forEach((image) => {
      formData.append("files", image);
    });

    //api call
    try {
      const response = await axiosBaseURl.post("/review/", formData, {
        headers: {
          Authorization: `bearer ${localStorage.getItem("ks_user_token")}`,
          "Content-Type": "multipart/form-data",
        },
      });
      setLoading(false);
      setErrorMsg("");
      setReviewTitle("");
      setReviewText("");
      setFeatureValue({});
      setFile([]);
      setImagePreview([]);
      console.log(response);
      setloginScreen("successScreen");
    } catch (err) {
      setLoading(false);
      setErrorMsg(err.response.data.message + ". Try Again");
      console.log(err);
    }
  };

  //Function for adding image files in array state
  const addImageFiles = (e) => {
    if (e.target.files.length !== 0) {
      setFile((existingItems) => {
        return [...existingItems, e.target.files[0]];
      });
      setImagePreview((existingItems) => {
        return [...existingItems, URL.createObjectURL(e.target.files[0])];
      });
    }
  };

  //Function for removing files from array state
  const deleteImageFiles = (id) => {
    setFile([...file.filter((item) => file.indexOf(item) !== id)]);
    setImagePreview([
      ...imagePreview.filter((item) => imagePreview.indexOf(item) !== id),
    ]);
  };

  //FUNC TO Refresh reviews after successful review posting
  const refreshReviews = () => {
    fetchTextReviews();
    setTimeout(() => {
      setloginScreen("addreview");
    }, 200);
  };
  // console.log("haha", isUser);
  // console.log("haha", imagePreview);

  return (
    <div
      className='modal fade'
      id='loginModal'
      tabIndex='-1'
      role='dialog'
      aria-hidden='true'
    >
      <div className='modal-dialog modal-dialog-centered' role='document'>
        <div className='modal-content'>
          <div className='modal-header'>
            <h5 className='modal-title' id='loginModalHeader'>
              {loginScreen === "addreview" ||
              localStorage.getItem("ks_user_token")
                ? "Add your review"
                : "Login to add your review"}
            </h5>
            {loginScreen === "successScreen" ? (
              <div
                type='button'
                className='close'
                data-bs-dismiss='modal'
                aria-label='Close'
                onClick={refreshReviews}
              >
                <Cross />
              </div>
            ) : (
              <div
                type='button'
                className='close'
                data-bs-dismiss='modal'
                aria-label='Close'
              >
                <Cross />
              </div>
            )}
          </div>
          <div className='modal-body'>
            {loginScreen === "successScreen" &&
            localStorage.getItem("ks_user_token") ? (
              <div className='text-center'>
                <img
                  src='https://thumbstacktech.github.io/Widget-bundle-files/confetti.gif'
                  alt='Confetti'
                  style={{ width: "150px" }}
                />
                <h6 className='m-4'>Hurray! Your review has been posted</h6>
              </div>
            ) : loginScreen === "verifyotp" &&
              !localStorage.getItem("ks_user_token") ? (
              <form
                id='loginForm'
                className='form-border '
                // novalidate was-validated
                onSubmit={(e) => {
                  e.preventDefault();
                  verifyOtp();
                }}
              >
                <div className='mb-4 icon-group'>
                  {!isUser && (
                    <div>
                      <label htmlFor='firstName'>New user details</label>
                      <input
                        type='text'
                        className='form-control mb-2'
                        id='firstName'
                        required
                        placeholder='Enter first name'
                        onChange={(e) =>
                          setUserInfo({
                            ...userInfo,
                            firstName: e.target.value,
                          })
                        }
                        value={userInfo.firstName}
                      />
                      <input
                        type='text'
                        className='form-control mb-4'
                        id='lastName'
                        required
                        placeholder='Enter last name'
                        onChange={(e) =>
                          setUserInfo({
                            ...userInfo,
                            lastName: e.target.value,
                          })
                        }
                        value={userInfo.lastName}
                      />
                      <label htmlFor='firstName'>OTP</label>
                    </div>
                  )}
                  <input
                    type='number'
                    className='form-control'
                    id='phoneNumber'
                    required
                    placeholder='Enter OTP'
                    onChange={(e) => setOtp(e.target.value)}
                    value={otp}
                  />
                  <p className='error-msg text-danger m-0'>{loginErrorMsg}</p>
                </div>
                <div className='text-center'>
                  <p className='mw-100'>
                    A 6 digit verification code sent to your mobile number{" "}
                    {phoneNo}.
                  </p>
                  {canResend ? (
                    <p className='mw-100 mt-3 text-danger'>
                      Not Recived Yet?{" "}
                      {otpcountdown === 0 ? (
                        <button
                          type='reset'
                          className='ks_otp_resetbtn btn p-0 text-danger'
                          onClick={() => sendOtp("true")}
                        >
                          {loading ? (
                            <span
                              className='spinner-border spinner-border-sm'
                              role='status'
                              aria-hidden='true'
                            />
                          ) : (
                            <strong>Resend</strong>
                          )}
                        </button>
                      ) : (
                        `Resend in ${otpcountdown} secs`
                      )}
                    </p>
                  ) : null}
                </div>
                <div className='d-grid mt-4 mb-3'>
                  <button
                    className='signIn btn btn-primary text-white text-uppercase py-3'
                    type='submit'
                  >
                    {verifyLoading ? (
                      <span
                        className='spinner-border spinner-border-sm'
                        role='status'
                        aria-hidden='true'
                      />
                    ) : (
                      "Verify"
                    )}
                  </button>
                </div>
              </form>
            ) : (loginScreen === "sendotp" &&
                !localStorage.getItem("ks_user_token")) ||
              loginScreen === "successScreen" ||
              (loginScreen === "addreview" &&
                !localStorage.getItem("ks_user_token")) ? (
              <form
                id='loginForm'
                className='form-border '
                onSubmit={(e) => {
                  e.preventDefault();
                  sendOtp();
                }}
              >
                <div className='mb-4 icon-group'>
                  <input
                    type='text'
                    maxLength='10'
                    pattern='\d{10}'
                    title='Please enter exactly 10 digits'
                    className='form-control'
                    id='phoneNumber'
                    required
                    placeholder='Enter Phone Number'
                    onChange={(e) => setPhoneNo(e.target.value)}
                    value={phoneNo}
                  />
                  <p className='error-msg text-danger m-0'>{loginErrorMsg}</p>
                </div>
                <p className='text-center mw-100'>
                  A 6 digit verification code will be send to your entered phone
                  number.
                </p>
                <div className='d-grid mt-4 mb-3'>
                  <button
                    className='signIn btn btn-primary text-white text-uppercase py-3'
                    type='submit'
                  >
                    {loading ? (
                      <span
                        className='spinner-border spinner-border-sm'
                        role='status'
                        aria-hidden='true'
                      />
                    ) : (
                      "Submit"
                    )}
                  </button>
                </div>
              </form>
            ) : loginScreen === "addreview" ||
              localStorage.getItem("ks_user_token") ? (
              <form
                id='addReviewForm'
                className='form-border'
                onSubmit={(e) => {
                  e.preventDefault();
                  addReview();
                }}
              >
                <div className='mb-4 icon-group'>
                  <label htmlFor='productTitle'>
                    Product Title: {brandName && brandName}
                  </label>
                  {/* <input
                    type='text'
                    className='form-control'
                    id='productTitle'
                    required
                    placeholder='Enter title of product'
                    value={brandName && brandName}
                  /> */}
                </div>
                <div className='mb-4 icon-group'>
                  <label htmlFor='reviewTitle'>Review Title</label>
                  <input
                    type='text'
                    className='form-control'
                    id='reviewTitle'
                    required
                    placeholder='Title of your review'
                    onChange={(e) => setReviewTitle(e.target.value)}
                    value={reviewTitle}
                  />
                </div>
                <div className='mb-4 icon-group'>
                  <label htmlFor='reviewDesc'>Review Description</label>
                  <textarea
                    rows='3'
                    type='text'
                    className='form-control'
                    id='reviewDesc'
                    required
                    placeholder='Enter detailed descrption'
                    onChange={(e) => setReviewText(e.target.value)}
                    value={reviewText}
                  />
                </div>
                <div className='mb-4 icon-group'>
                  <label>Product Ratings</label>

                  <div className={styles.productFeatureRatings}>
                    {featureData &&
                      featureData.map((item, idx) => {
                        return (
                          <div
                            key={idx}
                            className='d-flex justify-content-between align-items-center mt-2'
                          >
                            <p className='d-inline'>{item.name}</p>
                            <Rating
                              onChange={(rate) =>
                                setFeatureValue({
                                  ...featureValue,
                                  [item.name]: rate,
                                })
                              }
                              initialRating={featureValue[item.name]}
                              emptySymbol={<StarFullEmpty />}
                              fullSymbol={<StarFull />}
                            />
                          </div>
                        );
                      })}
                  </div>
                </div>
                <div className='mb-4 icon-group'>
                  <label>Your overall rating of this product</label>
                  <RatingStars rating={avgRating.toFixed(1)} />
                </div>
                <div className='mb-4 icon-group'>
                  <label>Do you have any photos to share (Optional)</label>
                  <label
                    htmlFor={styles.formFile}
                    className='btn btn-primary mt-1'
                  >
                    Upload Photos
                  </label>
                  <input
                    type='file'
                    id={styles.formFile}
                    onChange={addImageFiles}
                  />
                  <div className='mt-2'>
                    {imagePreview.map((item, idx) => {
                      return (
                        <div key={idx} className={styles.fileWrapper}>
                          <img
                            className={styles.previewImage}
                            src={item}
                            alt=''
                          />
                          <div
                            onClick={() => deleteImageFiles(idx)}
                            className={styles.deleteBtn}
                          >
                            <Cross />
                          </div>
                        </div>
                      );
                    })}
                  </div>
                </div>
                <div className='mb-4 icon-group'>
                  <label>Submit your review</label>
                  <div className='d-flex gap-2'>
                    <input
                      type='checkbox'
                      htmlFor='agreement'
                      required
                      name='agree'
                      id='agree'
                    />
                    <p id='agreement' className='mw-100'>
                      By clicking to this checkbox you agree to{" "}
                      <a
                        href='https://policies.google.com/privacy'
                        target='_blank'
                        rel='noreferrer'
                      >
                        {" "}
                        Google Privacy Policy
                      </a>{" "}
                      ,{" "}
                      <a
                        href='https://www.youtube.com/t/terms'
                        target='_blank'
                        rel='noreferrer'
                      >
                        YouTube Terms of Service(ToS)
                      </a>{" "}
                      And{" "}
                      <a
                        href='https://www.khooobsooorat.com/terms-conditions/'
                        target='_blank'
                        rel='noreferrer'
                      >
                        khooobsooorat's T&amp;C
                      </a>
                    </p>
                  </div>
                </div>
                <p className='error-msg text-danger m-0'>{errorMsg}</p>
                <div className='d-grid mt-4 mb-3'>
                  <button
                    //   disabled={userPass === ""}
                    className='signIn btn text-white text-uppercase py-3'
                    type='submit'
                  >
                    {loading ? (
                      <span
                        className='spinner-border spinner-border-sm'
                        role='status'
                        aria-hidden='true'
                      />
                    ) : (
                      "Submit your review"
                    )}
                  </button>
                </div>
              </form>
            ) : null}
          </div>
        </div>
      </div>
    </div>
  );
};

export default LoginForm;
