import React, { useState } from "react";
import Rating from "../Rating";
import styles from "../../../index.module.css";
import cx from "classnames";
import "animate.css";
import moment from "moment";
import { Chevron, Cross, Delete, Eye } from "../../svg";
import jwtDecode from "jwt-decode";
import axiosBaseURl from "../../axios";

const ReviewCardComponent = (props) => {
  const {
    id,
    rating,
    view_count,
    customer_id,
    created,
    review,
    review_type,
    ks_customers,
    ks_review_image,
    ks_filter_tags,
  } = props.cardData;
  const [expand, setExpand] = useState(false);
  const [loading, setLoading] = useState(false); //state for loading
  const handleClick = () => {
    setExpand(!expand);
  };

  // Set placeholder image in case of broken image links
  const addDefaultSrc = (ev) => {
    ev.target.src = "https://www.khooobsooorat.com/images/user-img-icon.png";
  };

  //decode jwt token
  try {
    // Check if no token
    if (!localStorage.getItem("ks_user_token")) {
      console.log("No token available");
    } else {
      var token = localStorage.getItem("ks_user_token");
    }
    var decoded = jwtDecode(token);
  } catch (error) {
    console.log(error);
  }

  const deleteReview = async () => {
    setLoading(true);
    //api call
    try {
      const response = await axiosBaseURl.delete(`/review/deleteReview/${id}`, {
        headers: {
          Authorization: `bearer ${localStorage.getItem("ks_user_token")}`,
        },
      });
      setLoading(false);
      console.log(response);
      props.fetchTextReviews();
    } catch (err) {
      setLoading(false);
      console.log(err);
    }
  };

  return (
    <div
      className={cx(styles.reviews__list__item, " row align-items-start m-0")}
    >
      <div
        className={cx(
          styles.avatar__wrapper,
          "col col-sm-2 d-flex flex-sm-column align-items-center"
        )}
      >
        <img
          className={styles.avatar}
          onError={addDefaultSrc}
          src={
            ks_customers.image_name
              ? `https://www.khooobsooorat.com/images/customers/${ks_customers.image_name}`
              : "https://www.khooobsooorat.com/images/user-img-icon.png"
          }
          alt='IMG'
        />
        <h5 className={styles.name}>{ks_customers.first_name}</h5>
      </div>
      <div className={cx(styles.textual, "col")}>
        <span
          onClick={handleClick}
          className={cx("chevron", expand ? "up" : null)}
        >
          <Chevron />
        </span>
        {review_type ? (
          <h4 onClick={handleClick} className={cx(styles.review__desc, "mb-2")}>
            {review_type}
          </h4>
        ) : (
          false
        )}
        <div className={cx(styles.rating__wrapper, " d-flex")}>
          <Rating rating={rating} />
          <p className={styles.date}>
            {moment(created).utc().format("DD/MM/YYYY")}
          </p>
          <div className={cx(styles.views, " d-flex gap-2 align-items-center")}>
            <Eye />
            <p className={styles.date}>{view_count}</p>
          </div>
          <div className={styles.verified}>
            Verified on <span className={styles.ksname}>Khooobsooorat</span>
          </div>
        </div>
        <div hidden={!expand} style={{ overflow: "hidden" }}>
          <div
            className={cx(
              styles.review__text__box,
              "animate__animated animate__fadeInDown"
            )}
          >
            {review}
          </div>
          <div
            className={cx(
              styles.review__image,
              "animate__animated animate__fadeInDown d-flex mt-2 gap-2"
            )}
          >
            {ks_review_image.map((item, idx) => {
              return (
                <img
                  key={idx}
                  onError={(e) => (e.target.style.display = "none")}
                  src={item.image_name}
                  alt='Images'
                />
              );
            })}
          </div>
        </div>

        <div className={cx(styles.tags, " d-flex")}>
          {ks_filter_tags
            .filter(
              (item) =>
                item.filter_tags !== "0" &&
                item.filter_tags !== null &&
                item.filter_tags !== "N/A" &&
                item.filter_tags !== "n/a"
            )
            .map((item, idx) => {
              return (
                <div key={idx} className={styles.tag__item}>
                  {item.filter_tags}
                </div>
              );
            })}
        </div>
        {decoded && customer_id === decoded.sub ? (
          <>
            <div
              type='button'
              data-bs-toggle='modal'
              data-bs-target='#deleteConfirm'
              className='delete__review'
            >
              <Delete />
            </div>
            <div
              className='modal fade'
              id='deleteConfirm'
              tabIndex='-1'
              role='dialog'
              aria-hidden='true'
            >
              <div
                className='modal-dialog modal-dialog-centered'
                role='document'
              >
                <div className='modal-content'>
                  <div className='modal-header'>
                    <h5 className='modal-title' id='loginModalHeader'>
                      Delete review
                    </h5>
                    <div
                      type='button'
                      className='close'
                      data-bs-dismiss='modal'
                      aria-label='Close'
                    >
                      <Cross />
                    </div>
                  </div>
                  <div className='modal-body'>
                    <div>
                      <h6 className='mb-5'>
                        Are you sure you want to delete this review?
                      </h6>
                      <div className='d-flex gap-2 justify-content-end'>
                        <div
                          type='button'
                          className='btn '
                          data-bs-dismiss='modal'
                          aria-label='Close'
                        >
                          Cancel
                        </div>
                        <div
                          type='button'
                          className='btn btn-danger'
                          onClick={deleteReview}
                          data-bs-dismiss='modal'
                        >
                          {loading ? (
                            <span
                              className='spinner-border spinner-border-sm'
                              role='status'
                              aria-hidden='true'
                            />
                          ) : (
                            "Delete"
                          )}
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </>
        ) : null}
      </div>
    </div>
  );
};

export default ReviewCardComponent;
